import React from "react";

import { useRenderer } from "./Renderer";
var glm = require ('gl-matrix');

var counter1 = 0;



function DemoEffect() {
  const { canvasRef } = useRenderer(
    
	`
#ifdef GL_ES
  precision mediump float;
  #endif
  
  uniform float time;
  uniform vec2 res;
  uniform vec3 testVec3;
  
  const float PI = acos(-1.0);
  const float PI2 = PI * 2.0;
  	

#define NUM_OCTAVES 5
float hash(float n) { return fract(sin(n) * 1e4); }
float hash(vec2 p) { return fract(1e4 * sin(17.0 * p.x + p.y * 0.1) * (0.1 + abs(sin(p.y * 13.0 + p.x)))); }


float noise(vec2 x) {
	vec2 i = floor(x);
	vec2 f = fract(x);

	// Four corners in 2D of a tile
	float a = hash(i);
	float b = hash(i + vec2(1.0, 0.0));
	float c = hash(i + vec2(0.0, 1.0));
	float d = hash(i + vec2(1.0, 1.0));

	// Simple 2D lerp using smoothstep envelope between the values.
	// return vec3(mix(mix(a, b, smoothstep(0.0, 1.0, f.x)),
	//			mix(c, d, smoothstep(0.0, 1.0, f.x)),
	//			smoothstep(0.0, 1.0, f.y)));

	// Same code, with the clamps in smoothstep and common subexpressions
	// optimized away.
	vec2 u = f * f * (3.0 - 2.0 * f);
	return mix(a, b, u.x) + (c - a) * u.y * (1.0 - u.x) + (d - b) * u.x * u.y;
}
  vec2 k_scope(vec2 p, float s, float offset) {
    float ma = abs(mod(atan(p.y, p.x), PI2 / s) - PI / s) + offset;
    return vec2(cos(ma), sin(ma)) * length(p);
  }
  
  varying vec3 fragmentPosition;
	varying vec3 fragmentNormal;
	varying vec2 fragmentTexCoord;
  
  uniform sampler2D diffuseTexture;
  void main( void ) {
    vec2 p = (gl_FragCoord.xy - 0.5 * res.xy) / res.y;
	vec3 norm = normalize(fragmentNormal);
	vec3 lightPos = vec3(0,4,4);
	vec3 lightDir = normalize(lightPos - fragmentPosition);  
	float diff = max(dot(norm, lightDir), 0.0);
	vec3 diffuse = diff * vec3(1,1,1);
   vec3 result = (vec3(0.5,0.5,0.5) + diffuse) * fragmentPosition;
	gl_FragColor =  vec4(vec3(texture2D(diffuseTexture, fragmentTexCoord)),1.0);;
	//gl_FragColor = vec4(result, 1.0);
   
   // gl_FragColor = vec4(0.8,fragmentPosition.y+0.6*fragmentNormal.y,fragmentPosition.z+0.6*fragmentNormal.z, 1.0);
  }
  `,
    function setUniforms(gl, start, prog) {
      let time = ((Date.now() - start)) / 1.0;
		
		var v3 = glm.vec3.create();
		v3[0]=(time)/1450.0;v3[1]=0.2;v3[2]=(time)/1750.0;
		
		// Projection Matrix
			
	var projection = glm.mat4.create();
	glm.mat4.perspective(projection,45.0,gl.canvas.width/gl.canvas.height,1,50.0);

	// View Matrix
	
	
	var i = glm.mat4.create();
	
	
	var view = glm.mat4.create();
	const eye = glm.vec3.create();
	eye[0]=0;eye[1]=0;eye[2]=-5;
	const center = glm.vec3.create();
	center[0]=0;center[1]=0;center[2]=0;
	const up = glm.vec3.create();
	up[0]=0;up[1]=1.0;up[2]=0;
	glm.mat4.targetTo(view,eye,center,up);
	var model = glm.mat4.create();
	
	
	var axis = glm.vec3.create();
	axis[0] = 0.35;//Math.sin(time/10.0);
	axis[1] = 1.0;
	axis[2]=0.0;
	
	glm.mat4.rotate(model,i,counter1++/10.0 ,axis);
	var translationVec3 = glm.vec3.create();
	translationVec3[0] =1*(Math.cos(0.001*time));
	translationVec3[1] = 1.35*Math.sin(0.0034*time);
	translationVec3[2]  =0;//=12*Math.abs(Math.sin(0.1*time));
	var identity = glm.mat4.create();
	var translate = glm.mat4.create();
	//glm.mat4.translate(model,identity,translationVec3);
	

	// Send uniforms
	  gl.uniformMatrix4fv(gl.getUniformLocation(prog, "projection"), false, projection);
	  gl.uniformMatrix4fv(gl.getUniformLocation(prog, "view"), false, view);
	  gl.uniformMatrix4fv(gl.getUniformLocation(prog, "model"), false, model);
      gl.uniform1f(gl.getUniformLocation(prog, "time"), time);
      gl.uniform3fv(gl.getUniformLocation(prog, "testVec3"), v3);
	  gl.uniform2fv(gl.getUniformLocation(prog, "res"), [
        gl.canvas.width,
        gl.canvas.height
      ]);
    }
  );

  return <canvas ref={canvasRef}></canvas>;
}

export default DemoEffect;



